@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;600;700;900&display=swap);
*, body, html {
  padding: 0;
  margin: 0;
}
body {
  margin: 0;
  font-family: 'Cairo', sans-serif,
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Cairo', sans-serif,
    monospace;
}

html {
  scroll-behavior: smooth;
  font-family: 'Cairo', sans-serif;
}

h1,h2,h3,h4,h5,h5,p {
  font-family: 'Cairo', sans-serif;
}


.w1 {
  -webkit-animation: rotate 9000ms infinite linear;
          animation: rotate 9000ms infinite linear;
  opacity: .1;
  
}

.w2 {
  -webkit-animation: rotate 7000ms infinite linear;
          animation: rotate 7000ms infinite linear;
  opacity: .1;
  
}

.w3 {

  -webkit-animation: rotate 9500ms infinite linear;

          animation: rotate 9500ms infinite linear;
}
.w4 {

  -webkit-animation: rotate 8000ms infinite linear;

          animation: rotate 8000ms infinite linear;
  background-color: #85a7d0;
}
.w5 {

  -webkit-animation: rotate 8500ms infinite linear;

          animation: rotate 8500ms infinite linear;
  background-color: #85a7d0;
}


@-webkit-keyframes rotate {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  from { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes rotate {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  from { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#project-button-left {
  left: -50px;
}
#project-button-right {
  right: 0;
}
.App {
  text-align: center;
  overflow: hidden;
  position: relative;
}

